.teaser-grid {
  margin: 1em 0;
  &__item {
    margin: 1em 0;
    @media screen and (min-width: 80.000em) {
      display: flex;
      justify-content: space-between;
    }
  }
  &__iframe {
    margin: 0 10px 0 0;
    overflow: auto;
    iframe {
      @media screen and (min-width: 80.000em) {
        max-width: 262px;
        max-height: 162px;
      }
    }
  }

  &__images {
    overflow: auto;
    margin: 0 10px 0 0;
    @media screen and (min-width: 80.000em) {
      width: 59%;
    }
  }
  &__thumbnail {
    width: 100%;
    height: auto;
  }

  &__container {
    overflow: auto;
    position: relative;
    @media screen and (min-width: 80.000em) {
      width: 39%;
    }
  }
  &__anchor {
    display: block;
  }
  &__title {
    color: #EC4E87;
    font-size: 21px;
  }
  &__subtitle {
    color: #3c3b3b;
  }
  &__anchor {}
}
